



































































































































































































import {
  defineComponent, reactive, computed, ref, toRefs, watch, onMounted,
} from '@vue/composition-api';
import { getContextUser } from '@/loaders/context';
import BaseImage from '@/components/base/Image';
import setLang, {
  languageNumber2lang,
  lang2TranslatedLang,
  langMapLangNum,
  langConfigList,
} from '@/router/utils/set-lang';
import i18n from 'i18next';

export type OperateType = 'review'|'edit';

export default defineComponent({
  name: 'meet-home__header',
  components: { BaseImage },
  props: {
    operateType: {
      type: String as ()=>OperateType,
      default: 'review',
    },
    isEdit: {
      type: Boolean,
      default: false,
    },
    searchValue: {
      type: String,
      default: '',
    },
    col: {
      type: Object,
      default: () => ({}),
    },
    allCols: {
      type: Array as ()=> any[],
      default: () => [],
    },
    showCols: {
      type: Array as ()=> any[],
      default: () => [],
    },
    isSearch: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit, root }) {
    const state = reactive({
      currentSearchValue: '',
      displayTypes: [{
        title: '大图',
        colsNum: 4,
        icon: 'icondatu',
      }, {
        title: '中图',
        colsNum: 8,
        icon: 'iconzhongtu',
      }, {
        title: '小图',
        colsNum: 16,
        icon: 'iconxiaotu',
      }],
      showDisplayTypes: false,
      colsNum: 8 as 4 | 8 | 16,
      temCurrentAllCols: [] as any[], // 用于保存打开时的数据,在取消时恢复
      currentAllCols: [] as any[],
      currentCol: {} as any,
      isShowAllCols: false,
      tmpLang: 0,
      oldLang: 0,
    });
    const user = computed(() => getContextUser());

    const userImage = computed(() => user.value?.headImg || require('@/assets/img/header/default_avatar.png'));
    const userTitle = computed(() => user.value?.name || user.value?.account || '未知用户');

    onMounted(() => {
      state.tmpLang = langMapLangNum(i18n.language).toString();
      state.oldLang = state.tmpLang; // 用于取消确认时回退
    });

    const setting = reactive({
      show: false,
      open() {
        state.temCurrentAllCols = JSON.parse(JSON.stringify(state.currentAllCols));
      },
      done() {
        const showCols = state.currentAllCols.filter(item => item.isShow);
        setting.show = false;
        emit('cols-change', showCols);
      },
      cancel() {
        state.currentAllCols = JSON.parse(JSON.stringify(state.temCurrentAllCols));
        setting.show = false;
      },
    });
    watch(() => props.searchValue, () => {
      state.currentSearchValue = props.searchValue;
    }, { immediate: true });
    watch(() => props.allCols, () => {
      state.currentAllCols = props.allCols.map(item => ({ ...item, isShow: true }));
      const showCols = state.currentAllCols.filter(item => item.isShow);
      emit('cols-change', showCols);
    }, { immediate: true });
    watch(() => props.col, () => {
      state.currentCol = props.col;
    });
    watch(() => state.currentAllCols, () => {
      const showCols = state.currentAllCols.filter(item => item.isShow);
      state.isShowAllCols = showCols.length === props.allCols.length;
    }, { immediate: true, deep: true });
    const changeType = (operateType:OperateType) => {
      emit('type-change', operateType);
    };
    const cancelEdit = () => {
      emit('cancel-edit');
    };
    const commitEdit = () => {
      emit('commit-edit');
    };
    const search = () => {
      emit('search', state.currentSearchValue);
    };
    const changeCols = (colsNum:4|8|16) => {
      state.colsNum = colsNum;
      state.showDisplayTypes = false;
      emit('cols-num-change', colsNum);
    };
    const changeIsShowAllCols = (isShowAllCols:boolean) => {
      state.currentAllCols.forEach(item => { item.isShow = isShowAllCols; });
    };
    const toMeetStore = () => {
      console.log('toMeetStore');
    };
    const handleSetLang = async (langNum:any) => {
      const lang:any = languageNumber2lang(langNum);
      try {
        await root.$confirm(i18n.t('setting.confirm_change_language', {
          lang: i18n.t(`setting.${lang2TranslatedLang(lang)}`),
        }), i18n.t('setting.confirm'));


        setLang(lang, async () => {
          await root.$router.replace({
            query: {
              ...root.$route.query,
              language: undefined, // 为防止和url的language形成死循环，需要去掉url的
            },
          });
          window.location.reload();
        });
      } catch (e) {
        state.tmpLang = state.oldLang;
      }
    };
    return {
      ...toRefs(state),
      setting,
      changeType,
      cancelEdit,
      commitEdit,
      search,
      changeCols,
      changeIsShowAllCols,
      toMeetStore,
      userImage,
      userTitle,
      langConfigList,
      handleSetLang,
    };
  },
});
